<template>
  <div class="container">
    <div class="table-wrapper mb30">
      <template>
        <el-row class="form-header">
          <el-col :span="24" class="tc">
            - 應付帳款申請明細 -
          </el-col>
        </el-row>

        <table class="table-group">
          <tr class="table-head">
            <th class="tb-w-20">申請日期</th>
            <th class="tb-w-20">會計項目</th>
            <th class="tb-w-20">員工/廠商</th>
            <th class="tb-w-20">明細</th>
            <th class="tb-w-20">金額</th>
          </tr>
          <tr v-for="item in tableBillingData" :key="item.id">
            <td>{{ item.dateValue }}</td>
            <td>{{ item.category }}</td>
            <td>{{ item.requisitioner }}</td>
            <td>{{ item.itemName }}</td>
            <td>{{ item.ntdTotal }}</td>
          </tr>

        </table>

      </template>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'AllListTable',
  data() {
    return {
      tableBillingData: [],
    }
  },
  methods: {
    // 取得審核過後的資料
    getVerifiedData() {
      axios.get('http://localhost:3000/verifiedData').then(res => {
        let data = res.data
        data.forEach((element, index) => {
          // 日期轉換格式
          let dateVal = element.dateValue
          let year = dateVal.slice(0,4)
          let month = dateVal.slice(5,7)
          let day = dateVal.slice(8,10)
          let finalDate = `${year}/${month}/${day}`
          
          this.tableBillingData.push({
            dateValue: finalDate,
            category: element.category,
            requisitioner: element.requisitioner,
            itemName: element.itemName,
            ntdTotal: element.ntdTotal
          })
        })
      })
    },
    
  },
  mounted() {
    this.getVerifiedData();
  }
}
</script>

