<script>
import axios from "axios";
import qs from "qs";
import { extend } from "vee-validate";
import { required, min_value, integer, numeric } from "vee-validate/dist/rules";
import { ValidationProvider, ValidationObserver } from "vee-validate";

extend("required", {
  ...required,
  message: "此選項為必填"
});

extend("integer", {
  ...integer,
  message: "必須是整數"
});

extend("numeric", {
  ...numeric,
  message: "必須是數字"
});

extend("min_value", {
  ...min_value,
  message: "數值最小為1"
});

extend("validate_tax", value => {
  if (value < 0) return "數值不能為負數";
  else if (isNaN(value) == true) return "請輸入數字";
  else return true;
});

extend("validate_price", value => {
  if (value <= 0) return "數值不能為0";
  else if (isNaN(value) == true) return "請輸入數字";
  else return true;
});

