      let DD = (dt.getDate() < 10 ? "0" : "") + dt.getDate();
      let hr = (dt.getHours() < 10 ? "0" : "") + dt.getHours();
      let mt = (dt.getMinutes() < 10 ? "0" : "") + dt.getMinutes();
      let sec = (dt.getSeconds() < 10 ? "0" : "") + dt.getSeconds();
      let now = `${YY}${MM}${DD}${hr}${mt}${sec}`;

      this.newItems.forEach((element, index) => {
        let nowId = now + "0" + index;
        element.id = nowId;
      });
