          // 幣種換算匯率
          // this.newItems[i].exchangeRate = this.newItems[i].currency;
          // 新台幣完稅運算
          this.newItems[i].ntdTotal =
            "NT$ " +
            Math.ceil(
              (parseFloat(
                this.newItems[i].price *
                  this.newItems[i].number *
                  this.newItems[i].exchangeRate
              ) +
                (parseFloat(
                  this.newItems[i].price *
                    this.newItems[i].number *
                    this.newItems[i].exchangeRate
                ) *
                  parseFloat(this.newItems[i].tax)) /
                  100) *
                100
            ) /
              100;
        }
      },
      deep: true // 深度監聽(監聽newItems[]裡面所有的數值)
    },
    payee: {
      handler() {
        if (this.payee == "2") {
          this.payeeVendor = "";
        }
      }
    },
    paymentWay: {
      handler() {
        if (this.paymentWay != "3") {
          this.remittanceAccount = "";
        }
      }
    },
    dateValue: {
      handler() {
        let year = this.dateValue.slice(0,4)
        let month = this.dateValue.slice(5,7)
        let day = this.dateValue.slice(8,10)
        this.dateValueFormat = `${year}/${month}/${day}`
        console.log(this.dateValueFormat)
      }
    }
  },
  mounted() {}
};
</script>

