<template>
  <div class="login">
    <el-row class="login-container" type="flex" align="middle" justify="center">
      <el-col class="contain">
        <el-row type="flex" justify="center" align="middle" class="mb30"> 
          <el-col :span="5">
            <span class="header-capital">A</span>
          </el-col>
          <el-col :span="12">
            <span class="header-title"> 應收應付系統</span>
            <p class="header-para">TaiPay Accounting Sysyem</p>
          </el-col>
        </el-row>
      
        <el-row class="mb15">
          <el-input
            placeholder="帳號"
            v-model="account">
            <i slot="prefix" class="el-input__icon el-icon-user"></i>
          </el-input>
        </el-row>
        <el-row class="mb15">
          <el-input
            placeholder="密碼"
            v-model="password"
            type="password"
            show-password >
            <i slot="prefix" class="el-input__icon el-icon-unlock"></i>
          </el-input>
        </el-row>
        <el-row>
          <el-button class="w100" type="primary" @click="userLogin" :loading="loading">登 入</el-button>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'
export default {
  name: "login",
  data() {
    return {
      account: "",
      password: "",
      loading: false
    }
  },
  methods: {
    userLogin() {

      // 獲取用戶資訊
      let user = {
        login: this.account,
        password: this.password
      }
      // 將用戶資訊轉換成DataForm
      let userObj = qs.stringify(user)
      // 發送POST請求至login API
      axios({
        method: 'post',
        url: 'http://192.168.2.42:20080/users/login',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        },
        data: userObj
      })
      .then(res => {
        console.log(res)
        if(res.data.status == 1 && res.status === 200) {

          localStorage.token = res.data.message

          this.loading = true
          this.$router.push({
            name: 'list'
          })
          
        } else {
          alert('帳號密碼錯誤')
        }
      }).catch(err => {
        console.log(err)
      })

    }
  },
  mounted() {
    window.addEventListener('keyup',event => {
      if(event.keyCode == 13) {
        this.userLogin()
      }
    })
  }
}
</script>

