<template>
  <div class="homeHeader">
    <el-header height="auto" class="header-container">
      <el-row class="row-bg" type="flex" justify="space-between">
        <div>
          <span class="header-capital">A</span>
          <span class="header-title"> {{ headerMSG }}</span>
        </div>
        <div>
          <el-dropdown>
            <span class="el-dropdown-link">
              {{ userName }}<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">              
              <div @click="logout">
              <el-dropdown-item icon="el-icon-switch-button">
                  登出
              </el-dropdown-item>
              </div>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-row>
    </el-header>
  </div>
</template>

<script>
import qs from 'qs'
import axios from 'axios'
export default {
  name: "pageHeader",
  data() {
    return {
      headerMSG: '應收應付系統',
      userName: ''
    };
  },
  mounted() {
    this.getUserInfo();
  },
  methods: {
    logout() {
      localStorage.clear("token");
      this.$router.push({
        name: "login"
      })
    },
    getUserInfo() {
      let userToken = localStorage.getItem("token")
      let userInfoObj = qs.stringify({ token: userToken })
      console.log(userInfoObj)
      axios
        .post("http://192.168.2.42:20080/users/getUser", userInfoObj)
        .then(req => {
          this.userName = JSON.parse(req.data.message).login
        });
    }
  }
};
</script>

