<script>
import axios from 'axios'
export default {
  name: "detailInfo",
  data() {
    return {
      loading: false,
      corporation: "", // 事業體
      category: "", // 類別
      payee: "", // 支付對象
      payeeVendor: "", // 支付對象-廠商名稱
      entryCategory: "", //入帳類別
      paymentWay: "", // 支付方法
      remittanceAccount: "", // 匯款帳號
      requisitioner: "", // 請購款人
      pickerOptions: {
        // 月曆
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      dateValue: "", // 日期
      departments: [
        {
          // 所有部門
          value: "董事長室",
          label: "董事長室"
        },
        {
          value: "財務部",
          label: "財務部"
        },
        {
          value: "人事部",
          label: "人事部"
        },
        {
          value: "軟體開發部",
          label: "軟體開發部"
        },
        {
          value: "商業開發部",
          label: "商業開發部"
        }
      ],
      department: "", // 部門
      newItems: [
        {
          id: "", // 項目id
          itemName: "", // 項目名稱
          currency: "", // 幣種
          price: "", // 單價
          number: "", // 數量
          exchangeRate: "", // 匯率
          tax: "", // 稅額
          ntdTotal: "" // 台幣總價(完稅)
        }
      ]
    };
  },
  methods: {
    getDetailInfo() {
     axios.get('http://localhost:3000/purchaseData?itemId='+ this.$route.params.id).then(res => {
       let data = res.data[0]
       console.log(data)
        this.corporation = data.corporation  // 事業體
        this.category = data.category // 類別
        this.payee = data.payee, // 支付對象
        this.payeeVendor = data.payeeVendor, // 支付對象-廠商名稱
        this.entryCategory = data.entryCategory, //入帳類別
        this.paymentWay = data.paymentWay, // 支付方法
        this.remittanceAccount = data.remittanceAccount, // 匯款帳號
        this.requisitioner = data.requisitioner, // 請購款人
        this.dateValue = data.dateValue, // 日期
        this.department = data.department
        this.newItems[0].itemName = data.itemName, // 項目名稱
        this.newItems[0].currency = data.currency, // 幣種
        this.newItems[0].price = data.price, // 單價
        this.newItems[0].number = data.number, // 數量
        this.newItems[0].exchangeRate = data.exchangeRate, // 匯率
        this.newItems[0].tax = data.tax, // 稅額
        this.newItems[0].ntdTotal = data.ntdTotal // 台幣總價(完稅)

     })
    }
  },
  mounted() {
    this.getDetailInfo()
  }
};
</script>

