<template>
  <div class="container">
    <div class="table-wrapper mb30">
      <template>
        <el-row class="form-header">
          <el-col :span="24" class="tc">
            - 申請中 -
          </el-col>
        </el-row>

        <table class="table-group">
          <tr class="table-head">
            <th class="tb-w-20">ID</th>
            <th class="tb-w-5">類別</th>
            <th class="tb-w-15">項目</th>
            <th class="tb-w-10">單價</th>
            <th class="tb-w-5">數量</th>
            <th class="tb-w-10">稅金</th>
            <th class="tb-w-5">匯率</th>
            <th class="tb-w-10">總價</th>
            <th class="tb-w-20">操作</th>
          </tr>
          <tr v-for="item in tableApplyingData" :key="item.id">
            <td>{{ item.itemId }}</td>
            <td>{{ item.category }}</td>
            <td>{{ item.itemName }}</td>
            <td>{{ item.price }}</td>
            <td>{{ item.number }}</td>
            <td>{{ item.tax }}</td>
            <td>{{ item.exchangeRate }}</td>
            <td>{{ item.ntdTotal }}</td>
            <td>
              <span>
                <el-button size="mini" type="success" @click="confirmMsgBox(item.id)">審核</el-button>
              </span>
              <span>
                <el-button class="mg5" size="mini" type="danger" @click="delMsgBox(item.id)">作廢</el-button>              
              </span>
              <router-link class="icon-view" tag="span" :to="/detail/ + item.itemId" >
                <i class="el-icon-view"></i>
              </router-link>
            </td>
          </tr>

        </table>

      </template>
    </div>

    <el-row class="btn-group">
      <el-button @click="upPage" :disabled="isFirstPage">上一頁</el-button>
      <el-button @click="downPage" :disabled="isLastPage">下一頁</el-button>
    </el-row>

  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "listApplying",
  data() {
    return {
      tableApplyingData: [
        // {
        //   id: "",
        //   category: "",
        //   itemName: "",
        //   price: "",
        //   number: "",
        //   tax: "",
        //   exchangeRate: "",
        //   ntdTotal: ""
        // },
      ],
      start: 0,
      isLastPage: false,
      isFirstPage: true
    };
  },
  methods: {
    getApi() {
      axios
        .get(`http://localhost:3000/purchaseData?_start=${this.start}&_limit=5`)
        .then(res => {
          let data = res.data;

          data.forEach((element, index) => {
              this.tableApplyingData.push({
                itemId: element.itemId,
                category: element.category,
                itemName: element.itemName,
                price: element.price,
                number: element.number,
                tax: element.tax,
                exchangeRate: element.exchangeRate,
                ntdTotal: element.ntdTotal,
                id: element.id
              })
          });

          if(data.length == 0){
            console.log('lastOne!!')
            this.isLastPage = true
          } else {
            this.isLastPage = false
          }
          console.log('start數值為：' + this.start)
          
        });

    },
    // 上一頁
    upPage() {
      this.start -= 5
      if(this.start <= 0) {
        this.start = 0
        this.isFirstPage = true
      }
      this.tableApplyingData.splice(0, this.tableApplyingData.length)
      this.getApi()
    },
    // 下一頁
    downPage() {
      this.start += 5
      this.isFirstPage = false
      this.tableApplyingData.splice(0, this.tableApplyingData.length)
      this.getApi() 
    },
    // 作廢訊息框
    delMsgBox(id) {
     
      this.$confirm('確定要刪除此項目？', '提示', {
        confirmButtonText: '確定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '删除成功!'
        }),
        this.deleteData(id);
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消刪除'
        });          
      });
    },
    // 審核訊息框
    confirmMsgBox(id) {
        this.$confirm('確定項目審核通過？', '提示', {
          confirmButtonText: '確定',
          cancelButtonText: '取消',
          type: 'warning',
        }).then(() => {
          this.$message({
            type: 'success',
            message: '審核通過!'
          }),
          this.confirmData(id);
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消操作'
          });          
        });
      },
      // 審核資料
      confirmData(id) {
        axios.get('http://localhost:3000/purchaseData/' + id).then(res => {
          let data = res.data
          let dataDetail = {
            "corporation": data.corporation,
            "category": data.category,
            "payee": data.payee,
            "payeeVendor": data.payeeVendor,
            "entryCategory": data.entryCategory,
            "paymentWay": data.paymentWay,
            "remittanceAccount": data.remittanceAccount,
            "requisitioner": data.requisitioner,
            "dateValue": data.dateValue,
            "itemId": data.itemId,
            "itemName": data.itemName,
            "currency": data.currency,
            "price": data.price,
            "number": data.number,
            "exchangeRate": data.exchangeRate,
            "tax": data.tax,
            "ntdTotal": data.ntdTotal,
            "id": data.id
          }
          axios.post('http://localhost:3000/verifiedData', dataDetail).then(res => {
            console.log(res.data)
          }).then(
              setTimeout(() => {
              window.location.reload()
            }, 2000)
          )

          axios.delete('http://localhost:3000/purchaseData/' + id)
        })
      },
      // 作廢資料
      deleteData(id) {
        axios.delete('http://localhost:3000/purchaseData/' + id).then(()=>{
          window.location.reload()
        })
      }
  },
  mounted() {
    this.getApi();
  }
};
</script>

